import double from "assets/images/bestcase/double.png";
import React, { useEffect, useState } from "react";
// import { Link } from "react-router-dom";
import { default as Slider } from "react-slick";
import { useLocalize } from "store/actions/localizeAction";
import "./BestCase.scss";
import LazyImage from "libs/lazy-load/LazyImage";

const WOW = require("wowjs/dist/wow.js");
export interface CaseDataType {
  name: string;
  quote: string;
  quoteAuthor: string;
  title: string;
  visual: {
    mimeType: string;
    url: string;
  };
  awards: [
    {
      name: string;
      image: {
        url: string;
      };
    }
  ];
}
export interface BestCaseDataType {
  label: {
    intro: string;
  };
  button: {
    discover: {
      text: string;
      href: string;
    };
  };
  bestcases: CaseDataType[];
}
const BestCase: React.FC = () => {
  useEffect(() => {
    const wow = new WOW.WOW({
      live: false,
    });
    wow.init();
  }, []);

  const [state, setstate] = useState();
  const [state1, setstate1] = useState();
  const data = useLocalize<BestCaseDataType>("stories");

  const settings = {
    dots: true,
    customPaging: () => {
      return (
        <a href="#section">
          <div className="com-value-dots white"></div>
        </a>
      );
    },
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    arrows: false,
  };
  const settingsReward = {
    draggable: true,
    arrows: false,
    dots: true,
    fade: true,
    speed: 900,
    infinite: true,
    cssEase: "cubic-bezier(0.7, 0, 0.3, 1)",
    touchThreshold: 100,
  };
  const renderListItem = () => {
    return data.bestcases.map((item: CaseDataType, index) => {
      //check have ""
      let haveDouble = false;
      if (item.quote.includes('"')) {
        item.quote = item.quote.replace(/"/g, "");

        haveDouble = true;
      }
      return (
        <div className="item" key={index}>
          <div className="name">{item.name}</div>
          <div className="title">{item.title}</div>
          {haveDouble && <LazyImage className="double" src={double} alt="double" />}
          <div className={`content ${haveDouble && "double"}`}>{item.quote}</div>
          {haveDouble &&
            <LazyImage className="last-double" src={double} alt="double"></LazyImage>
          }
          <div className="person">{item?.quoteAuthor}</div>
        </div>
      );
    });
  };

  const renderListReward = () => {
    return data.bestcases.map((item: CaseDataType, index) => {
      return (
        <div className="item-reward" key={index}>
          <div className="wrapper-img">
            <LazyImage className="img-bg" src={item.visual.url} alt="bg-game"></LazyImage>
          </div>

          <div className="block-name">
            {item.awards.length > 0 && (
              <div className="block-reward">
                <LazyImage className="img-reward" src={item.awards[0]?.image.url} alt="reward"></LazyImage>
              </div>
            )}
          </div>
        </div>
      );
    });
  };

  return (
    <section className="com-bestcase">
      <div className="title">{data?.label.intro}</div>

      {data?.bestcases && (
        <div className="block-game">
          <div className="wrapper-reward">
            <Slider className="block-item thumbnail_slider" {...settingsReward} ref={(slider: any) => setstate(slider)} asNavFor={state1}>
              {renderListReward()}
            </Slider>
          </div>

          <div className="wrapper-right">
            <Slider className="block-item" {...settings} asNavFor={state} ref={(slider: any) => setstate1(slider)}>
              {renderListItem()}
            </Slider>
            {/* <Link to="/best-case">
              <div className="btn-bestcase-desk">{data?.button.discover.text}</div>
            </Link> */}
          </div>
        </div>
      )}

      {/* <Link to="/best-case">
        <div className="btn-bestcase">{data?.button.discover.text}</div>
      </Link> */}
    </section>
  );
};

export default BestCase;
